.region-name {
  text-align: center;
  padding: 30px 0;
  background-color: rgb(213, 200, 83);
}

.btn-container {
  padding-top: 30px;
  display: flex;
  align-items: center;
  width: 50%;
}

.btn {
  padding: 5px;
  border-radius: 5px;
  background-color: rgb(237, 172, 92);
  margin-right: 5px;
  font-weight: bold;
  font-size: 24px;
}

.content-container {
  text-align: center;
  padding: 30px;
}

.gomi-img {
  width: 20%;
  height: auto;
}

.category {
  font-weight: bold;
  font-size: 20px;
}

.way {
  font-weight: bold;
  font-size: 18px;
}